@import url("https://fonts.googleapis.com/css2?family=Gugi&display=swap");

.page-wrap {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10px;
}

h1 {
  font-family: "Gugi", cursive;
  font-size: 4em;
  color: #15d5a2;
}

@media only screen and (max-width: 600px) {
  h1 {
    font-size: 3em;
  }
}
